.card {
    background: #fff;
    border-radius: 2px;
    height: 20%;
    margin: 10px;
    position: relative;
    width: 20%;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999; 
  }
  
   .card > .img-container {
    overflow: hidden;
    text-align: center;
    background:#6CADDC;
  } 
  
  .card > .img-container > img {
    width: 100%
    
  }
  
  .card > .content {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 15px;
  }
  
  .card > .content > ul {
    list-style-type: none;
  }
  
  .card > .content > ul > li {
    margin-top: 18px;
  }
  
  .card > .remove {
    color: #ff1744;
    font-size: 48px;
    position: absolute;
    top: -20px;
    right: 5px;
    cursor: pointer;
  }
  
  .card > .remove:hover {
    color: #d50000;
  }
.jumbotron {
    width: 100%;
    text-align: center;
    font-size: 1rem;
    text-decoration: none; 
    color: black;
    margin-bottom: 10px;
    background-color: rgb(244, 244, 154); 
    clear: "both";
    background-image:url(/Memory-Clicky-Game/static/media/croppedWaves.578c9c9d.jpg);
    z-index: 99;
  }
  
.wrapper {
    padding-top: 50px;
    background: #ECEFF1;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    padding: 20px;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
    /* background-color: rgb(107, 175, 216); */
    background-image: url(/Memory-Clicky-Game/static/media/underTheSea.e4502eff.jpg);
  }
.title {
    width: 100%;
    text-align: center;
    margin-bottom: 50px;
  }
  
* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

h1{
  font-weight:900;
}

